.mainSec2 {
    background-color: #013574;
    min-height: 50vh;
    min-width: 50vw;
    height: auto;
    width: auto;
    padding: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.Section2 {
    background-color: #013574;
    color: white;
    display: flex;
    align-items: center;
    gap: 2rem;
    margin: 20px;
    min-height: 20vh;
    height: auto;
    padding: 3rem;
    border: 1px dashed white;
    justify-content: space-between;
}

.sec2img {
    height: 280px;
    width: 780px;
    background-color: white;
    border-radius: 8px;
}

.mainhHead h2 {
    margin-bottom: 10px;
    font-size: 2rem;
}

.mainPara1 {
    padding-bottom: 20px;
    font-size: 20px;
}

.sec2Btn button {
    background: none;
    background-color: white;
    color: black;
    text-transform: uppercase;
    padding: 10px 20px 10px 20px;
}

@media screen and (max-width:1300px) {
    .mainSec2 {
        padding: 0;
        flex-wrap: wrap;
    }

    .Section2 {
        flex-wrap: wrap;
    }
}