nav {
    background: #151414;
    color: whitesmoke;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding: 0 3rem 0 0;
    position: fixed;
    top: 0;
    /* bottom: 0; */
    left: 0;
    right: 0;
    max-width: 100vw;
    width: auto;
    z-index: 11111;
    /* min-height: 100vh; */
}

nav img {
    width: 200px;
}

nav ul {
    display: flex;
    gap: 3rem;
    align-items: center;
    justify-content: center;
}

nav ul li {
    list-style: none;
    font-size: medium;
    font-weight: 200;
    text-transform: uppercase;
    cursor: pointer;
}

.iconsHamburger {
    display: none;
}

@media screen and (max-width:1300px) {
    .navUl {
        position: fixed;
        top: 0;
        /* height: 5vh; */
        overflow: visible;
        margin-top: 6.5rem;
        display: flex;
        flex-direction: column;
        /* left: 0; */
        right: 0;
        color: black;
        padding: 30px;
        transform: scaleX(0);
        transform-origin: right;
        background-color: white;
        transition: all 0.3s ease;
    }

    .close {
        /* background-color: white; */
        overflow: hidden;
        font-size: 2rem;
        cursor: pointer;
    }

    .iconsHamburger {
        display: flex;
    }

    .navUl-open {
        transform: scaleX(1);
    }
}