.MainFlex {
    background-color: #030d42;
    min-height: 100vh;
    min-width: 90vw;
    height: auto;
    width: auto;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-evenly;
}

h1 {
    /* text-align: left !important; */
    /* background-color: aqua; */
    font-size: 45px;
    color: whitesmoke;
    width: 100%;
}

.topFlex {
    display: flex;
    flex-wrap: wrap;
    gap: 2rem;
    align-items: center;
}

.bottomFlex {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 2rem;
}

.individual {
    height: 200px;
    width: 210px;
    border-radius: 8px;
    background-color: whitesmoke;
}