.HeroContainer {
    background: url('../images/section\ 1\ bg.png');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    min-height: 90vh;
    height: auto;
    width: 100vw;
    width: auto;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    flex-direction: row;
    justify-content: space-between;
    /* background: #000; */
    color: white;
    overflow: hidden;
}

.HeroContainer img {
    width: 750px;
    margin-top: 4rem;
    margin-left: 4rem;
}

.HeroMainDetails {
    margin-top: 10rem;
    margin-right: 15rem;
    color: white;
}

.HeroMainDetails p {
    font-size: 2rem;
    font-weight: bolder;
}

.HeroMainDetails button {
    background: none;
    background-color: #054490;
    border: none;
    margin-top: 10px;
    padding: 10px;
    border-radius: 18px;
    color: white;
}

@media screen and (max-width:1300px) {
    .HeroContainer {
        flex-direction: column;
        align-items: center;
        height: 100vh;
        width: 100vw;
        justify-content: center;
    }

    .HeroMainDetails {
        margin: 0;
    }

    .HeroMainDetails p {
        font-size: 2rem;
        width: 100vw;
        text-align: center;
        font-weight: bolder;
    }

    .HeroContainer img {
        width: 100%;
        margin-top: 3rem;
    }

    .HeroMainDetails button {
        width: 100%;
    }
}