.imgDiv img {
    height: 200px;
    width: 200px;
    border-radius: 8px;
    background-color: black;
}

.mainSec3 {
    background-color: #030d42;
    color: white;
    min-height: 100vh;
    min-width: 100vw;
    height: auto;
    width: auto;
    padding: 40px;
}

.mainHead h2 {
    font-size: 30px;
    margin-bottom: 10px;
}

.mainHead p {
    text-transform: uppercase;
    font-size: 10px;
}

.MainFlex {
    /* background-color: red; */
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    padding: 2rem;
}

.topFlex {
    display: flex;
    justify-content: space-around;
    padding: 2rem;
}

.MainFlex h2 {
    text-transform: uppercase;
    padding: 10px 0 0 20px;
}

.MainFlex p {
    /* background-color: red; */
    text-transform: capitalize;
    padding: 10px 0 0 20px;
    margin-right: 4rem;
    font-size: medium;
}

.bottomFlex {
    display: flex;
    padding: 0 2rem 0 2rem;
    justify-content: space-around;
}